.overlay{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.1);
	z-index: 1;
	opacity: 0;
	visibility: hidden;
	overflow: hidden;
	transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
}

.overlayVisible{
	visibility: visible;
	opacity: 1;

	.drawer{
		transform: translateX(0%);
	}
}

.drawer{
	display: flex;
	flex-direction: column;
	position: absolute;
	right: 0;
	width: 420px;
	height: 100%;
	background: #FFFFFF;
	box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
	padding: 30px;
	transform: translateX(100%);
	transition: transform 0.3s ease-in-out;

	h2{
		margin: 0;
	}

	.items{
		flex:1;
		overflow: auto;
	}
}